import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import { Box, Card, Container, Divider } from '@mui/material'

import { SETTING_MESSAGE } from 'src/data/messages'

import ChangeClubDate from 'src/components/DateForm/ChangeClubDate'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import PageHeader from 'src/content/manage/PageHeader'
import SearchableSetting from 'src/content/manage/Settings/SearchableSetting'

import { useLogout } from 'src/hooks/api/authHooks'
import { usePatchClub } from 'src/hooks/api/club'

const SettingsPage: React.FC = () => {
  const authUser = useAuthUser()
  const params = useParams()
  const clubId = Number(params.id)
  const club = authUser()?.clubs.filter((c: { id: number }) => c.id === clubId)[0]
  const logoutMutation = useLogout(SETTING_MESSAGE.LOGOUT)

  const patchClubMutation = usePatchClub()

  const handleSwitchChange: React.ChangeEventHandler<HTMLInputElement> = async e => {
    try {
      await patchClubMutation.mutateAsync({ clubId, data: { searchable: e.target.checked } })
      // TODO : 설정할 때마다 로그아웃 되지 않고, useUpdateAuthState 잘 완성시켜서 로그아웃 안 되기 하기
      logoutMutation.mutate()
    } catch (error) {}
  }

  return (
    <>
      <Helmet>
        <title>동아리 설정</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader
          clubName={club.name}
          buttonText={'회원등급 관리'}
          link={`/manage/${clubId}/settings/member-level`}
        />
      </PageTitleWrapper>
      <Container maxWidth={'lg'}>
        <Card sx={{ minHeight: '400px' }}>
          {/* <ClubSummary /> */}
          <Box mx={2} my={2}>
            <SearchableSetting
              handleSwitchChange={handleSwitchChange}
              defaultChecked={club.searchable}
              clubName={club.name}
            />
          </Box>
          <Divider />
          <Box mx={2} my={2}>
            <ChangeClubDate clubId={clubId} />
          </Box>
          <Divider />
        </Card>
      </Container>
    </>
  )
}

export default SettingsPage
